
// ##################### Import Shared ##################### 

@import "sass/shared";

// ##################### Home Page ##################### 

.show-section {
  $mq: 
    small (
      padding-top: $info-content-distance,
      padding-bottom: $info-content-distance
    ),
    $tablet (
      padding-top: $info-content-distance-tablet,
      padding-bottom: $info-content-distance-tablet
    ),
    $minilaptop (
      padding-top: $info-content-distance-minilaptop,
      padding-bottom: $info-content-distance-minilaptop
    ),
    $laptop (
      padding-top: $info-content-distance-laptop,
      padding-bottom: $info-content-distance-laptop
    );

  background: url(./img/man-in-black-coat-standing-beside-white-wall-4021514-min.jpg);
  background-size: cover;
  background-position: top left;
  background-repeat: no-repeat;
  min-height: 100%;
  animation: animateImg 5s linear forwards;

  @include applyMQ($mq) 
}

@keyframes animateImg {
  0% {
    background-position: top left;
  }
  100% {
    background-position: center;
  }
}

.info-content {
  transform: translateX(-100vw);
  padding: 0.75em;
  background: rgba(0, 0, 0, 0.7);
  color: $whitish;
  border-radius: 10px;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
  animation: moveFromLeft 3s forwards;
  
  $mq: 
    small (
      max-width: 400px
    ),
    $tablet (
      max-width: 500px,
      margin: 0 auto
    ),
    $minilaptop (
      max-width: 750px,
      padding: 1.2em
    ),
    $laptop (
      max-width: 850px
    );
  @include applyMQ($mq);

  & + & {
    $mq: 
      small (
        margin-top: $info-content-distance
      ),
      $tablet (
        margin-top: $info-content-distance-tablet
      ),
      $minilaptop (
        margin-top: $info-content-distance-minilaptop
      ),
      $laptop (
        margin-top: $info-content-distance-laptop
      );

    @include applyMQ($mq);
  }
}

@for $i from 1 through 3 {
  .info-content:nth-of-type(#{$i}) {
    animation-delay: #{$i}s;
  }
}


@keyframes moveFromLeft {
  from {
      transform: translateX(-100vw)
  }
  to {
      transform: translateX(0%)
  }
}

.info-header {
  padding-bottom: .1em;
  margin-bottom: .3em;
  border-bottom: 2px groove $coralish;
  font-size: 1.1rem;
}

.info-par {
  font-size: .9rem;
}

.social-icons {
  padding: .2em;
  background-color: #424242;

  > .container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
  }
}

.social-icon {
  width: 6%;
  flex: 1 0 20px;
  max-width: 30px;
  cursor: pointer;
  outline: none;
  background-color: transparent;
}

@media (min-width: 1000px) {
  .info-header {
      font-size: 1.4rem;
  }
  .info-par {
      font-size: 1rem;
  }

}

@media (min-width: 1240px) {
  .info-header {
      font-size: 1.6rem;
  }
  .info-par {
      font-size: 1.2rem;
  }
}
