html, body {
  height: 100%;
  font-family: Arial, Helvetica, sans-serif;
}

img {
  display: inline-block;
  max-width: 100%;
}

a {
  text-decoration: none;
}

.container {
  width: 94%;
  max-width: 1200px;
  margin: 0 auto;
}

// I had to create and apply this class because if
// we set overflow-x prop in 'html, body' block then
// window.pageYOffset property always returns 0.
// It's not good as I wanna fix the header on scroll.
.overflow-controller {
  overflow-x: hidden;
}

#skip a {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

#skip a:focus {
  position: static;
  width: auto;
  height: auto;
}

#to-top-button {
  position: fixed;
  right: 1.5rem;
  bottom: 1.5rem;
  height: 40px;
  width: 40px;
  background-color: #1c8aff;
  border-radius: 50%;
  text-align: center;
  line-height: 40px;
  color: $whitish;
  font-size: 1.4rem;
  font-weight: 900;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.4s linear;
}
