@mixin media($bp) {
  @media (min-width: $bp) {
    @content;
  }
}

@mixin applyMQ($mqMap) {
  @each $device, $styles in $mqMap{
    @if $device == small {
      @each $prop, $value in $styles {
        #{$prop}: $value;
      }
    } @else {
      @include media($device) {
        @each $prop, $value in $styles {
          #{$prop}: $value;
        } 
      }
    }
  }
}
