header {
  position: relative;
  background: $header-bg;
  padding: .5em 0;
  z-index: 32;
  border-bottom: 5px outset $coralish;

  &.fixed {
    position: fixed;
    width: 100%;
    left: 0;
    z-index: 99;
  }

  &.animate-fixing {
    animation: fixIt 0.5s forwards;
  }

  @include media($tablet) {
    padding-bottom: 0.2em;
  }
  
  > .container {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    flex-direction: row-reverse;

    @include media($tablet) {
      flex-direction: row;

      > div:nth-of-type(2) {
        width: 85%;
      }
    }
  }
}

@keyframes fixIt {
  from {
    top: -100%;
  } to {
    top: 0;
  }
}

.logo p {
  font-size: 1.5rem;
  font-weight: lighter;
  color: $coralish;
  font-family: serif;

  @include media($laptop) {
    font-size: 1.75rem;
  }

  @include media($desktop) {
    font-size: 2.5rem;
  }
}

#first-letter {
  color: $whitish;
  font-family: 'Times New Roman', Times, serif;
  font-weight: 900;

  @include media($laptop) {
    font-size: 1.6rem;
  }

  @include media($desktop) {
    font-size: 2.6rem;
  }
}

.hmbrgr-btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: $hamburger-size;
  width: $hamburger-size;

  @include media($tablet){
    display: none;
  }

  &__base, &__base::before, &__base::after {
    width: $hamburger-base-width;
    height: $hamburger-base-height;
    border-radius: $hamburger-base-height - 1;
    background-color: $whitish;
    transition: all ease-in .5s;
  }

  &__base {
    &::before, &::after {
      content: "";
      position: absolute;
    }

    &::before {
      transform: translateY(-$base-translate-y);
    }

    &::after {
      transform: translateY($base-translate-y);
    }
  }

  &[aria-expanded="true"] > &__base {
    background: transparent;
    
    &::before {
      transform: rotate(135deg);
    }

    &::after {
      transform: rotate(-135deg);
    }
  }

  &[aria-expanded="true"] + nav {
    transform: translateX(0);
  }
}

nav {
  position: absolute;
  transform: translateX(100vw);
  background: skyblue;
  transition: transform linear .4s;
  border-radius: 10px;
  z-index: 33;

  @include media($tablet) {
    transform: translateX(0%);
    position: static;
    background: transparent;
  }
}

.navul {
  list-style: none;

  @include media($tablet) {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navlist {
  padding: 1em;

  @include media($tablet) {
    padding: 0 0.2em;
  }
}

.navlist:not(:last-of-type) {
  border-bottom: 1px outset gray;

  @include media($tablet) {
    border-bottom: none;
  }
}

.navlink {
  display: block;

  @include media($tablet) {
    display: inline-block;
    color: $whitish;
    transition: color .4s;
    font-size: .8rem;

    &:hover {
      color: $coralish;
    }
  }

  @include media($laptop) {
    font-size: 1.1rem;
  }

  @include media($desktop) {
    font-size: 1.3rem;
  }
}

[class^="material-icons"] {
  float: right;
  margin: -4px 0 0 .6em;

  @include media($tablet) {
    font-size: 1.1rem;
    margin-top: 1px;
  }

  @include media($desktop) {
    font-size: 1.3rem;
  }
}

#active {
  color: $coralish;
  font-weight: 700;
}

#arka {
  position: fixed;
  height: 100%;
  width: 100%;
  display: none;
  background: rgba(0, 0, 0, 0.4);
  z-index: 1;
}
