*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul, ol {
  list-style: none;
}

button {
  border: none;
}

html, body {
  height: 100%;
  font-family: Arial, Helvetica, sans-serif;
}

img {
  display: inline-block;
  max-width: 100%;
}

a {
  text-decoration: none;
}

.container {
  width: 94%;
  max-width: 1200px;
  margin: 0 auto;
}

.overflow-controller {
  overflow-x: hidden;
}

#skip a {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

#skip a:focus {
  position: static;
  width: auto;
  height: auto;
}

#to-top-button {
  position: fixed;
  right: 1.5rem;
  bottom: 1.5rem;
  height: 40px;
  width: 40px;
  background-color: #1c8aff;
  border-radius: 50%;
  text-align: center;
  line-height: 40px;
  color: #dadada;
  font-size: 1.4rem;
  font-weight: 900;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.4s linear;
}

header {
  position: relative;
  background: rgba(0, 0, 0, 0.7);
  padding: 0.5em 0;
  z-index: 32;
  border-bottom: 5px outset #ff7020;
}
header.fixed {
  position: fixed;
  width: 100%;
  left: 0;
  z-index: 99;
}
header.animate-fixing {
  animation: fixIt 0.5s forwards;
}
@media (min-width: 768px) {
  header {
    padding-bottom: 0.2em;
  }
}
header > .container {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  flex-direction: row-reverse;
}
@media (min-width: 768px) {
  header > .container {
    flex-direction: row;
  }
  header > .container > div:nth-of-type(2) {
    width: 85%;
  }
}

@keyframes fixIt {
  from {
    top: -100%;
  }
  to {
    top: 0;
  }
}
.logo p {
  font-size: 1.5rem;
  font-weight: lighter;
  color: #ff7020;
  font-family: serif;
}
@media (min-width: 1240px) {
  .logo p {
    font-size: 1.75rem;
  }
}
@media (min-width: 1440px) {
  .logo p {
    font-size: 2.5rem;
  }
}

#first-letter {
  color: #dadada;
  font-family: "Times New Roman", Times, serif;
  font-weight: 900;
}
@media (min-width: 1240px) {
  #first-letter {
    font-size: 1.6rem;
  }
}
@media (min-width: 1440px) {
  #first-letter {
    font-size: 2.6rem;
  }
}

.hmbrgr-btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 30px;
  width: 30px;
}
@media (min-width: 768px) {
  .hmbrgr-btn {
    display: none;
  }
}
.hmbrgr-btn__base, .hmbrgr-btn__base::before, .hmbrgr-btn__base::after {
  width: 20px;
  height: 3px;
  border-radius: 2px;
  background-color: #dadada;
  transition: all ease-in 0.5s;
}
.hmbrgr-btn__base::before, .hmbrgr-btn__base::after {
  content: "";
  position: absolute;
}
.hmbrgr-btn__base::before {
  transform: translateY(-5px);
}
.hmbrgr-btn__base::after {
  transform: translateY(5px);
}
.hmbrgr-btn[aria-expanded=true] > .hmbrgr-btn__base {
  background: transparent;
}
.hmbrgr-btn[aria-expanded=true] > .hmbrgr-btn__base::before {
  transform: rotate(135deg);
}
.hmbrgr-btn[aria-expanded=true] > .hmbrgr-btn__base::after {
  transform: rotate(-135deg);
}
.hmbrgr-btn[aria-expanded=true] + nav {
  transform: translateX(0);
}

nav {
  position: absolute;
  transform: translateX(100vw);
  background: skyblue;
  transition: transform linear 0.4s;
  border-radius: 10px;
  z-index: 33;
}
@media (min-width: 768px) {
  nav {
    transform: translateX(0%);
    position: static;
    background: transparent;
  }
}

.navul {
  list-style: none;
}
@media (min-width: 768px) {
  .navul {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navlist {
  padding: 1em;
}
@media (min-width: 768px) {
  .navlist {
    padding: 0 0.2em;
  }
}

.navlist:not(:last-of-type) {
  border-bottom: 1px outset gray;
}
@media (min-width: 768px) {
  .navlist:not(:last-of-type) {
    border-bottom: none;
  }
}

.navlink {
  display: block;
}
@media (min-width: 768px) {
  .navlink {
    display: inline-block;
    color: #dadada;
    transition: color 0.4s;
    font-size: 0.8rem;
  }
  .navlink:hover {
    color: #ff7020;
  }
}
@media (min-width: 1240px) {
  .navlink {
    font-size: 1.1rem;
  }
}
@media (min-width: 1440px) {
  .navlink {
    font-size: 1.3rem;
  }
}

[class^=material-icons] {
  float: right;
  margin: -4px 0 0 0.6em;
}
@media (min-width: 768px) {
  [class^=material-icons] {
    font-size: 1.1rem;
    margin-top: 1px;
  }
}
@media (min-width: 1440px) {
  [class^=material-icons] {
    font-size: 1.3rem;
  }
}

#active {
  color: #ff7020;
  font-weight: 700;
}

#arka {
  position: fixed;
  height: 100%;
  width: 100%;
  display: none;
  background: rgba(0, 0, 0, 0.4);
  z-index: 1;
}

footer {
  padding: 0.3em;
  letter-spacing: 0.1em;
  font-size: 0.7em;
  background: linear-gradient(to bottom, #000, #424242);
  color: #dadada;
  text-align: center;
  font-family: serif;
}
@media (min-width: 1240px) {
  footer {
    font-size: 1.3rem;
  }
}

.show-section {
  background: url(./img/man-in-black-coat-standing-beside-white-wall-4021514-min.jpg);
  background-size: cover;
  background-position: top left;
  background-repeat: no-repeat;
  min-height: 100%;
  animation: animateImg 5s linear forwards;
  padding-top: 2.25em;
  padding-bottom: 2.25em;
}
@media (min-width: 768px) {
  .show-section {
    padding-top: 3em;
    padding-bottom: 3em;
  }
}
@media (min-width: 1024px) {
  .show-section {
    padding-top: 4.5em;
    padding-bottom: 4.5em;
  }
}
@media (min-width: 1240px) {
  .show-section {
    padding-top: 6em;
    padding-bottom: 6em;
  }
}

@keyframes animateImg {
  0% {
    background-position: top left;
  }
  100% {
    background-position: center;
  }
}
.info-content {
  transform: translateX(-100vw);
  padding: 0.75em;
  background: rgba(0, 0, 0, 0.7);
  color: #dadada;
  border-radius: 10px;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
  animation: moveFromLeft 3s forwards;
  max-width: 400px;
}
@media (min-width: 768px) {
  .info-content {
    max-width: 500px;
    margin: 0 auto;
  }
}
@media (min-width: 1024px) {
  .info-content {
    max-width: 750px;
    padding: 1.2em;
  }
}
@media (min-width: 1240px) {
  .info-content {
    max-width: 850px;
  }
}
.info-content + .info-content {
  margin-top: 2.25em;
}
@media (min-width: 768px) {
  .info-content + .info-content {
    margin-top: 3em;
  }
}
@media (min-width: 1024px) {
  .info-content + .info-content {
    margin-top: 4.5em;
  }
}
@media (min-width: 1240px) {
  .info-content + .info-content {
    margin-top: 6em;
  }
}

.info-content:nth-of-type(1) {
  animation-delay: 1s;
}

.info-content:nth-of-type(2) {
  animation-delay: 2s;
}

.info-content:nth-of-type(3) {
  animation-delay: 3s;
}

@keyframes moveFromLeft {
  from {
    transform: translateX(-100vw);
  }
  to {
    transform: translateX(0%);
  }
}
.info-header {
  padding-bottom: 0.1em;
  margin-bottom: 0.3em;
  border-bottom: 2px groove #ff7020;
  font-size: 1.1rem;
}

.info-par {
  font-size: 0.9rem;
}

.social-icons {
  padding: 0.2em;
  background-color: #424242;
}
.social-icons > .container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.social-icon {
  width: 6%;
  flex: 1 0 20px;
  max-width: 30px;
  cursor: pointer;
  outline: none;
  background-color: transparent;
}

@media (min-width: 1000px) {
  .info-header {
    font-size: 1.4rem;
  }

  .info-par {
    font-size: 1rem;
  }
}
@media (min-width: 1240px) {
  .info-header {
    font-size: 1.6rem;
  }

  .info-par {
    font-size: 1.2rem;
  }
}