@use "sass:math";
$tablet: 768px;
$minilaptop: 1024px;
$laptop: 1240px;
$desktop: 1440px;

$header-bg: rgba(0, 0, 0, 0.7);
$whitish: #dadada;
$coralish: #ff7020;
$grayish: #343434;

$hamburger-size: 30px;
$hamburger-base-width: $hamburger-size - 10;
$hamburger-base-height: math.div($hamburger-size, 10);
$base-translate-y: math.div($hamburger-size, 6);

$info-content-distance: 2.25em;
$info-content-distance-tablet: $info-content-distance + 0.75;
$info-content-distance-minilaptop: $info-content-distance-tablet + 1.5;
$info-content-distance-laptop: $info-content-distance-minilaptop + 1.5;

